.header {
  background-color: #393E36;
  height: 80px;
  width: 100%;
  padding: 1.5em 4em;
}
.header .content-header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
  gap: 30em;
}
@media screen and (max-width: 1000px) {
  .header .content-header {
    gap: 20em;
  }
}

.menu-header .menus {
  display: flex;
  justify-content: center;
  gap: 2em;
  align-items: center;
}
@media screen and (max-width: 800px) {
  .menu-header .menus {
    display: none;
  }
}
.menu-header .menus a {
  text-align: center;
  color: #EEEEEE;
}
.menu-header .navbar {
  position: relative;
  padding: 1rem;
  display: none;
}
.menu-header .navbar .menu-toggle {
  cursor: pointer;
}
.menu-header .navbar .menu-toggle .bar {
  display: block;
  width: 35px;
  height: 5px;
  background-color: #EEEEEE;
  margin: 5px auto;
  transition: 0.3s;
  border-radius: 10px;
}
@media (max-width: 800px) {
  .menu-header .navbar {
    display: block;
  }
}

.header-menu-mobile {
  background-color: #222831;
  display: none;
}
.header-menu-mobile .menus-mobile {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1em 0;
  width: 100%;
}
.header-menu-mobile .menus-mobile a {
  text-align: center;
  color: #EEEEEE;
  width: 100%;
  border-bottom: 1px solid #EEEEEE;
  padding: 1.1em 0;
}
.header-menu-mobile .menus-mobile a:first-child {
  padding-top: 0;
}
.header-menu-mobile .menus-mobile a:last-child {
  border-bottom: none;
  padding: 0;
  padding-top: 1.1em;
}